import { useState } from 'react';
import { Handle } from 'reactflow';

export const WrapperNode = ({ id, data }) => {
    const { handleConfig, title, inputConfig, width = 200, height = 80 } = data;

    const [fields, setFields] = useState(() => {
        const initialState = {};
        inputConfig?.forEach(f => {
            initialState[f.name] = f.defaultValue;
        })
        return initialState;
    })


    const handleFieldChange = (field, value) => {
        setFields(prev => ({
            ...prev,
            [field]: value
        }))
    }

    return (
        <div style={{ width, height, border: '1px solid black' }}>
            <div>
                <span>{title}</span>
            </div>
            {inputConfig?.map((fl, idx) => (
                <div key={idx}>
                    <label>
                        {fl.label}:
                        {fl.type === "text" && (
                            <input type='text'
                                value={fields[fl.name] || ''}
                                onChange={(e) => handleFieldChange(fl.name, e.target.value)} />
                        )}
                        {fl.type === 'select' && (
                            <select
                                value={fields[fl.name] || ''}
                                onChange={(e) => handleFieldChange(fl.name, e.target.value)}
                            >
                                {fl.options?.map((opt, i) => (
                                    <option key={i} value={opt}>{opt}</option>
                                ))}
                            </select>
                        )}
                        {fl.type === 'number' && (
                            <input type='number'
                                value={fields[fl.name] || ''}
                                onChange={(e) => handleFieldChange(fl.name, e.target.value)} />
                        )}
                        {fl.type === 'checkbox' && (
                            <input type='checkbox'
                                value={fields[fl.name] || ''}
                                onChange={(e) => handleFieldChange(fl.name, e.target.value)} />
                        )}
                        {fl.type === 'date' && (
                            <input type='date'
                                value={fields[fl.name] || ''}
                                onChange={(e) => handleFieldChange(fl.name, e.target.value)} />
                        )}
                    </label>
                </div>
            ))}
            {handleConfig?.map((h, idx) => (
                <Handle key={idx}
                    type={h?.type}
                    position={h?.position}
                    id={`${id}-${h?.idValue}`}
                    style={{ top: `${h?.location}%` }}
                />
            ))}

        </div>
    )
}
