// ui.js
// Displays the drag-and-drop UI
// --------------------------------------------------

import { useState, useRef, useCallback } from 'react';
import ReactFlow, { Controls, Background, MiniMap } from 'reactflow';
import { useStore } from './store';
import { shallow } from 'zustand/shallow';
import { InputNode } from './nodes/inputNode';
import { LLMNode } from './nodes/llmNode';
import { OutputNode } from './nodes/outputNode';
// import { TextNode } from './nodes/textNode';

// newly created
import { WrapperNode } from './components/Node'
import { NumberNode } from './components/nodes/NumberNode'
import { TextNode } from './components/nodes/TextNode'



import 'reactflow/dist/style.css';
import { BooleanNode } from './components/nodes/BooleanNode';
import { DateNode } from './components/nodes/DateNode';
import { SelectNode } from './components/nodes/SelectNode';

const gridSize = 20;
const proOptions = { hideAttribution: true };
const nodeTypes = {
  customInput: InputNode,
  llm: LLMNode,
  customOutput: OutputNode,
  // text: TextNode,
  wrapper: WrapperNode,
  number: NumberNode,
  text: TextNode,
  checkbox: BooleanNode,
  date: DateNode,
  select: SelectNode
};

const selector = (state) => ({
  nodes: state.nodes,
  edges: state.edges,
  getNodeID: state.getNodeID,
  addNode: state.addNode,
  onNodesChange: state.onNodesChange,
  onEdgesChange: state.onEdgesChange,
  onConnect: state.onConnect,
});

export const PipelineUI = () => {
  const reactFlowWrapper = useRef(null);
  const [reactFlowInstance, setReactFlowInstance] = useState(null);
  const {
    nodes,
    edges,
    getNodeID,
    addNode,
    onNodesChange,
    onEdgesChange,
    onConnect
  } = useStore(selector, shallow);

  const getInitNodeData = (nodeID, type) => {
    let nodeData = {
      id: nodeID, nodeType: `${type}`
    };
    return nodeData;
  }

  const onDrop = useCallback(
    (event) => {
      event.preventDefault();

      const reactFlowBounds = reactFlowWrapper.current.getBoundingClientRect();
      if (event?.dataTransfer?.getData('application/reactflow')) {
        const appData = JSON.parse(event.dataTransfer.getData('application/reactflow'));
        const type = appData?.nodeType;

        // check if the dropped element is valid
        if (typeof type === 'undefined' || !type) {
          return;
        }

        const position = reactFlowInstance.project({
          x: event.clientX - reactFlowBounds.left,
          y: event.clientY - reactFlowBounds.top,
        });

        const nodeID = getNodeID(type);
        const newNode = {
          id: nodeID,
          type,
          position,
          data: {
            width: 500,
            ...getInitNodeData(nodeID, type),
            handleConfig: [{
              type: "target",
              position: "right",
              idValue: "value",
              location: `${100 / 3}`
            }, {
              type: "source",
              position: "left",
              idValue: "value",
              location: `${100 / 2}`
            }],
            inputConfig: [
              // {
              //   type: "text",
              //   label: "Name",
              //   name: "currName",
              //   defaultValue: nodeID.replace('customInput-', 'input_')
              // },
              // {
              //   type: "select",
              //   label: "Type",
              //   name: "inputType",
              //   defaultValue: "Text",
              //   options: ["Text", "File"]
              // }
            ],
            title: "wrapper"
          }
        }

        addNode(newNode);
      }
    },
    [reactFlowInstance]
  );

  const onDragOver = useCallback((event) => {
    event.preventDefault();
    event.dataTransfer.dropEffect = 'move';
  }, []);

  return (
    <>
      <div ref={reactFlowWrapper} style={{ width: '100wv', height: '70vh' }}>
        <ReactFlow
          nodes={nodes}
          edges={edges}
          onNodesChange={onNodesChange}
          onEdgesChange={onEdgesChange}
          onConnect={onConnect}
          onDrop={onDrop}
          onDragOver={onDragOver}
          onInit={setReactFlowInstance}
          nodeTypes={nodeTypes}
          proOptions={proOptions}
          snapGrid={[gridSize, gridSize]}
          connectionLineType='smoothstep'
        >
          <Background color="#aaa" gap={gridSize} />
          <Controls />
          <MiniMap />
        </ReactFlow>
      </div>
    </>
  )
}
